body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    
    font-size: 62.5%; 


}

body {
    box-sizing: border-box;
    padding: 3rem;

    background-color: rgba(244, 247, 245, 0.979);


}

.App{
    display: grid;
    grid-template-columns:repeat(4,1fr);
    grid-template-rows: 10rem  100rem;
    box-shadow: 4px 4px 10px rgb(202, 201, 201);
    background-color: rgba(244, 247, 245, 0.979);
}
.header{
    background-color: #21aa93;
    grid-column: 1/-1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 4rem;
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;

}
.side-bar{
    background-color: rgba(229, 235, 229, 0.911);
    grid-column: 1 / 1;
    grid-row: 2 / -1;
    display: flex;
    /* justify-content: space-around; */
    align-items: center;
    flex-direction: column;
    font-size: 2rem;
    padding: 4rem 1rem;
    box-sizing: border-box;
    /* display: none */
}
.person{
    width: 100%;
    height: 6rem;
    font-size: 1.6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 1rem;
    background-color:  rgba(241, 247, 241, 0.966);
    cursor: pointer;
}
.green{
    border-bottom: 4px solid rgb(33, 170, 147);

}
.red{
    border-bottom: 4px solid rgb(151, 47, 56);

}
.disabled{
    cursor: default
}

.side-bar h3{
    margin-bottom: 4rem;
}
.container{
    background-color: rgba(245, 244, 245, 0.911);
    grid-row: 2 / -1;
    grid-column: 2 / -1;
    display: grid;
    grid-template-rows: 60rem 40rem;
    grid-template-columns: 1fr;
    padding: 1rem;
}
.streams{
    /* background-color: teal; */
    display: flex;
    position: relative;
    font-size: 4rem;
    flex-wrap: wrap;   
}
.stream{
    width:49%;
    height: 49%;
    /* border: 2px solid gray; */
    /* -webkit-clip-path: polygon(100% 0, 100% 100%, 9% 100%, 9% 91%, 0 91%, 0 0);
    clip-path: polygon(100% 0, 100% 100%, 9% 100%, 9% 91%, 0 91%, 0 0); */
    background-color: aliceblue;
    display: flex;
    justify-content: center;
    box-sizing: border-box;
    overflow: hidden;
    margin: 0.4rem;
    /* box-shadow: 4px 4px 5px rgb(163, 163, 163); */

}
.map{
    width:49%;
    height: auto;
    /* border: 2px solid gray; */
    /* -webkit-clip-path: polygon(100% 0, 100% 100%, 9% 100%, 9% 91%, 0 91%, 0 0);
    clip-path: polygon(100% 0, 100% 100%, 9% 100%, 9% 91%, 0 91%, 0 0); */
    background-color: aliceblue;
    display: flex;
    justify-content: center;
    box-sizing: border-box;
    overflow: hidden;
    margin: 2rem 0.4rem;
    align-self: flex-end;
    
    border: 3px solid #21aa93;
    border-radius: 5px;
}
img {
    max-width: 100%;
    /* max-height: 100%; */
}

.controller{
    width: 100%;
    height: 100%;
    background-color: rgba(177, 175, 175, 0.466);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    display: none;
}

.streams:hover .controller{
    display:flex
    
}
.controller .play{
    background-image: url(data:image/png;base64,);
    background-color: transparent;
    background-size: cover;
    width: 15rem;
    height: 15rem;
    border: none;
    cursor: pointer;
}

.controller .pause{
    background-image: url(data:image/png;base64,);
    background-color: transparent;
    background-size: cover;
    width: 15rem;
    height: 15rem;
    border: none;
    cursor: pointer;
}
